import { Plugin } from '@ali/4ever-cangjie';
import { SubjectPlugin } from '@ali/4ever-bamboo';
import createRenderNode from './handlers/createRenderNode';
import { ZhiConfig } from '../../types';
import { SubjectConfig } from './types';
import * as queries from './queries';


const { createGroupNodes, createOnCangjieSelect, createRenderMark } = SubjectPlugin.handlers;

function createSubjectPlugin(config: ZhiConfig): Plugin {
  const { subject = {} } = config;

  return {
    groupNodes: createGroupNodes(),
    renderNode: createRenderNode(subject as SubjectConfig),
    renderMark: createRenderMark({ inject: true }),
    onCangjieSelect: createOnCangjieSelect({ inject: true }),
    queries,
  };
}

export default createSubjectPlugin;
